.scroll {
    display: flex;
    justify-content: center;
}
.scroll-down {
    margin-top: 160px;
    margin-bottom: 30px;
    background-repeat: no-repeat;
    background-position: center;
    width: 42px;
    height: 42px;
}
.scroll-down:hover path {
    
}
@media screen and (min-width: 768px) {
    .scroll-down {
        margin-top: 417px;
        margin-bottom: 30px;
    }
}
@media screen and (min-width: 1024px) {
    .scroll-down {
        margin-top: 290px;
        margin-bottom: 30px;
    }
}